'use strict';
angular.module('oneImobiliaria').filter('dateBr', function() {
  return function(input) {
    var date;
    if (input == null) {
      return '';
    }
    date = input.split('T');
    return moment(date[0]).format('DD/MM/YYYY');
  };
});

'use strict';
angular.module('oneImobiliaria').filter('dateTimeBr', function() {
  return function(input) {
    if (input == null) {
      return '';
    }
    moment.locale('pt-br');
    return moment(input).calendar(null, {
      sameElse: 'DD/MM/YYYY - H:mm'
    });
  };
});

'use strict';
angular.module('oneImobiliaria').filter('firstName', function() {
  return function(name) {
    var names;
    if ((name == null) || name === '') {
      return '';
    }
    names = name.split(' ');
    return names[0];
  };
});

'use strict';
angular.module('oneImobiliaria').filter('meters', function() {
  return function(meters) {
    if ((meters == null) || meters === '') {
      meters = '0';
    }
    return meters + ' m²';
  };
});

'use strict';
angular.module('oneImobiliaria').filter('orderByString', function() {
  return function(array, property, reverse) {
    var result;
    result = array.sort(function(object1, object2) {
      if (angular.isUndefined(property)) {
        object1.localeCompare(object2);
      }
      return object1[property].localeCompare(object2[property]);
    });
    if (reverse) {
      return result.reverse();
    } else {
      return result;
    }
  };
});

'use strict';
angular.module('oneImobiliaria').filter('phoneBr', function() {
  return function(input) {
    if (input == null) {
      return '';
    }
    if (input.length === 10) {
      return input.replace(/(\d{2})(\d{4})(\d{4})/g, "(\$1) \$2-\$3");
    } else {
      return input.replace(/(\d{2})(\d{5})(\d{4})/g, "(\$1) \$2-\$3");
    }
  };
});

'use strict';
angular.module('oneImobiliaria').filter('propertyType', [
  'PropertyTypeService', function(PropertyTypeService) {
    return function(propertyType) {
      var items, title;
      if ((propertyType == null) || propertyType === '') {
        return '';
      }
      items = PropertyTypeService.getAll();
      title = '';
      items.map(function(item) {
        if (item.code === propertyType) {
          return title = item.title;
        }
      });
      return title;
    };
  }
]);

'use strict';
angular.module('oneImobiliaria').filter('range', function() {
  return function(n) {
    var i, j, ref, res;
    res = [];
    for (i = j = 1, ref = n + 1; j < ref; i = j += 1) {
      res.push(i);
    }
    return res;
  };
});

'use strict';
angular.module('oneImobiliaria').filter('groupName', [
  'UserGroupService', function(UserGroupService) {
    return function(group) {
      var items, title;
      if ((group == null) || group === '') {
        return '';
      }
      items = UserGroupService.getAll();
      title = '';
      items.map(function(item) {
        if (item.code === group) {
          return title = item.title;
        }
      });
      return title;
    };
  }
]);

'use strict';
angular.module('oneImobiliaria').filter('yesOrNo', function() {
  return function(type) {
    if (type) {
      return 'Sim';
    } else {
      return 'Não';
    }
  };
});

'use strict';
angular.module('oneImobiliaria').directive('file', function() {
  return {
    require: "ngModel",
    restrict: 'A',
    link: function($scope, elem, attrs, ngModel) {
      var changeHandler;
      changeHandler = function(event) {
        var file, files;
        files = event.target.files;
        file = files[0];
        ngModel.$setViewValue(file);
        return $scope.$apply();
      };
      return elem.bind('change', changeHandler);
    }
  };
});

'use strict';
angular.module('oneImobiliaria').directive('meters', [
  '$parse', function($parse) {
    return {
      require: "?ngModel",
      scope: {
        ngModel: '='
      },
      restrict: 'A',
      link: function($scope, elem, attrs, ngModel) {
        var changeHandler, formatter;
        console.log(ngModel.$modelValue);
        formatter = function(value) {
          if (value == null) {
            value = 0;
          }
          value = value.toString().replace('m²', '');
          value = value.toString().replace(/\D/g, '');
          return value + 'm²';
        };
        ngModel.$formatters.push(function(value) {
          console.log(value);
          return formatter(value);
        });
        ngModel.$parsers.push(function(value) {
          return value.toString().replace('m²', '');
        });
        changeHandler = function() {
          var newValue;
          newValue = formatter(ngModel.$modelValue);
          console.log(newValue);
          $parse(attrs.ngModel).assign($scope, newValue);
          ngModel.$viewValue = newValue;
          return ngModel.$render();
        };
        return elem.bind('keyup', changeHandler);
      }
    };
  }
]);

'use strict';
angular.module('oneImobiliaria').controller('ClientsCtrl', [
  '$scope', '$rootScope', '$state', '$stateParams', '$filter', '$loading', '$logger', 'storage', 'ClientService', function($scope, $rootScope, $state, $stateParams, $filter, $loading, $logger, storage, ClientService) {
    $scope.client = {};
    $scope.clients = [];
    $scope.edit = true;
    $loading.show();
    if ($stateParams.id) {
      ClientService.get($stateParams.id).then(function(response) {
        $scope.client = response.data;
        $scope.edit = false;
        return $loading.hide();
      })["catch"](function(response) {
        $logger.error('Erro ao buscar clientes. Por favor, atualize a página.');
        return $loading.hide();
      });
    } else {
      ClientService.getAll().then(function(response) {
        $scope.clients = $filter('orderBy')(response.data, '-created');
        return $loading.hide();
      })["catch"](function(response) {
        $logger.error('Erro ao buscar clientes. Por favor, atualize a página.');
        return $loading.hide();
      });
    }
    $scope.canEdit = function() {
      return $scope.edit = true;
    };
    $scope.saveOrUpdate = function() {
      if (!$rootScope.forms.client.$valid) {
        $logger.error('Preencha todos os dados obrigatórios.');
        return;
      }
      $loading.show();
      return ClientService.saveOrUpdate($scope.client).then(function(response) {
        $loading.hide();
        return $state.go('dashboard.clients');
      })["catch"](function(response) {
        $logger.error('Erro ao criar/editar cliente. Por favor, tente novamente.');
        return $loading.hide();
      });
    };
    return $scope.doDelete = function(index) {
      var client;
      client = $scope.clients[index];
      if (client._id === storage.getCode()) {
        return false;
      }
      $loading.show();
      return ClientService["delete"](client._id).then(function(response) {
        $logger.success('Cliente excluído com sucesso!');
        $scope.clients.splice(index, 1);
        return $loading.hide();
      })["catch"](function(response) {
        $logger.error('Erro ao excluir cliente. Por favor, tente novamente.');
        return $loading.hide();
      });
    };
  }
]);

'use strict';
angular.module('oneImobiliaria').controller('LoginCtrl', [
  '$scope', '$rootScope', '$state', '$logger', 'storage', 'UserService', function($scope, $rootScope, $state, $logger, storage, UserService) {
    $scope.user = {};
    return $scope.doLogin = function() {
      if (!$rootScope.forms.login.$valid) {
        $logger.error('Preencha todos os dados obrigatórios.');
        return;
      }
      $scope.user.origin = 'admin';
      return UserService.doLogin($scope.user).then(function(response) {
        storage.setCodes(response.data);
        return $state.go('dashboard.home');
      })["catch"](function(response) {
        return $logger.error('Usuário e/ou senha inválido(s).');
      });
    };
  }
]);

'use strict';
angular.module('oneImobiliaria').controller('PropertiesCtrl', [
  '$scope', '$rootScope', '$state', '$stateParams', '$filter', '$loading', '$logger', 'storage', 'PropertyService', 'LocationService', 'ClientService', function($scope, $rootScope, $state, $stateParams, $filter, $loading, $logger, storage, PropertyService, LocationService, ClientService) {
    var cities;
    $scope.property = {
      "code": "123",
      "client": "585ecf5bd5af8351e3b894e3",
      "type": "apartament",
      "meters": 100,
      "vacancy": "1",
      "floor": "2",
      "address": {
        "street": "Rua Teste",
        "number": "123",
        "state": "SP",
        "city": "São Paulo",
        "neighborhood": "Jardim Teste",
        "cep": "03952020"
      },
      "hasSubway": true,
      "subwayStation": "Penha",
      "value": 1,
      "condominium": 2,
      "iptu": 3,
      "location": 4,
      "payments": ["financing", "money", "others"],
      "exchange": 0.1,
      "difference": 0.5,
      "carValue": 5,
      "settled": true,
      "car": true
    };
    $scope.properties = [];
    $scope.cities = [];
    $scope.states = [];
    $scope.clients = [];
    cities = [];
    $scope.edit = true;
    $loading.show();
    if ($stateParams.id) {
      PropertyService.get($stateParams.id).then(function(response) {
        $scope.property = response.data;
        return LocationService.getAllStates();
      }).then(function(response) {
        $scope.states = response.data.sort();
        return LocationService.getCitiesByState($scope.property.address.state);
      }).then(function(response) {
        var currentCities;
        currentCities = $filter('orderByString')(response.data, 'name');
        $scope.cities = currentCities;
        cities[$scope.property.address.state] = currentCities;
        return ClientService.getAll();
      }).then(function(response) {
        $scope.clients = $filter('orderByString')(response.data, 'name');
        $scope.edit = false;
        return $loading.hide();
      })["catch"](function(response) {
        $logger.error('Erro ao buscar imóveis. Por favor, atualize a página.');
        return $loading.hide();
      });
    } else {
      PropertyService.getAll().then(function(response) {
        $scope.properties = $filter('orderBy')(response.data, '-created');
        return LocationService.getAllStates();
      }).then(function(response) {
        $scope.states = response.data.sort();
        return ClientService.getAll();
      }).then(function(response) {
        $scope.clients = $filter('orderByString')(response.data, 'name');
        return $loading.hide();
      })["catch"](function(response) {
        $logger.error('Erro ao buscar imóveis. Por favor, atualize a página.');
        return $loading.hide();
      });
    }
    $scope.showCities = function(state) {
      if (cities[state] != null) {
        $scope.cities = cities[state];
        return false;
      }
      $loading.show();
      return LocationService.getCitiesByState(state).then(function(response) {
        var currentCities;
        currentCities = $filter('orderByString')(response.data, 'name');
        $scope.cities = currentCities;
        cities[state] = currentCities;
        return $loading.hide();
      })["catch"](function() {
        return $loading.hide();
      });
    };
    $scope.canEdit = function() {
      return $scope.edit = true;
    };
    $scope.saveOrUpdate = function() {
      if (!$rootScope.forms.property.$valid) {
        $logger.error('Preencha todos os dados obrigatórios.');
        return;
      }
      $loading.show();
      return PropertyService.saveOrUpdate($scope.property).then(function(response) {
        $loading.hide();
        return $state.go('dashboard.properties');
      })["catch"](function(response) {
        $logger.error('Erro ao criar/editar imóvel. Por favor, tente novamente.');
        return $loading.hide();
      });
    };
    return $scope.doDelete = function(index) {
      var property;
      property = $scope.properties[index];
      if (property._id === storage.getCode()) {
        return false;
      }
      $loading.show();
      return PropertyService["delete"](property._id).then(function(response) {
        $logger.success('Imóvel excluído com sucesso!');
        $scope.properties.splice(index, 1);
        return $loading.hide();
      })["catch"](function(response) {
        $logger.error('Erro ao excluir imóvel. Por favor, tente novamente.');
        return $loading.hide();
      });
    };
  }
]);

'use strict';
angular.module('oneImobiliaria').controller('UsersCtrl', [
  '$scope', '$rootScope', '$state', '$stateParams', '$filter', '$loading', '$logger', 'storage', 'UserGroupService', 'UserService', function($scope, $rootScope, $state, $stateParams, $filter, $loading, $logger, storage, UserGroupService, UserService) {
    $scope.user = {};
    $scope.users = [];
    $scope.groups = UserGroupService.getAll();
    $scope.edit = true;
    $loading.show();
    if ($stateParams.id) {
      UserService.get($stateParams.id).then(function(response) {
        $scope.user = response.data;
        $scope.edit = false;
        return $loading.hide();
      })["catch"](function(response) {
        $logger.error('Erro ao buscar usuários. Por favor, atualize a página.');
        return $loading.hide();
      });
    } else {
      UserService.getAll().then(function(response) {
        $scope.users = $filter('orderBy')(response.data, '-created');
        return $loading.hide();
      })["catch"](function(response) {
        $logger.error('Erro ao buscar usuários. Por favor, atualize a página.');
        return $loading.hide();
      });
    }
    $scope.canEdit = function() {
      return $scope.edit = true;
    };
    $scope.saveOrUpdate = function() {
      if (!$rootScope.forms.user.$valid) {
        $logger.error('Preencha todos os dados obrigatórios.');
        return;
      }
      $loading.show();
      return UserService.saveOrUpdate($scope.user).then(function(response) {
        $loading.hide();
        return $state.go('dashboard.users');
      })["catch"](function(response) {
        $logger.error('Erro ao criar/editar usuário. Por favor, tente novamente.');
        return $loading.hide();
      });
    };
    return $scope.doDelete = function(index) {
      var user;
      user = $scope.users[index];
      if (user._id === storage.getCode()) {
        return false;
      }
      $loading.show();
      return UserService["delete"](user._id).then(function(response) {
        $logger.success('Usuário excluído com sucesso!');
        $scope.users.splice(index, 1);
        return $loading.hide();
      })["catch"](function(response) {
        $logger.error('Erro ao excluir usuário. Por favor, tente novamente.');
        return $loading.hide();
      });
    };
  }
]);
